import React from 'react'
import "./Button.css"
import catAudio from "../assets/cat.mp3"
import lionAudio from "../assets/lion.mp3"

const Button = (props) => {
    let cl = "button" + (props.inverted ? " inverted" : "");

    const cat = new Audio(catAudio);
    const lion = new Audio(lionAudio);
    
    const change = () => {
        props.changeColor(props.color);
        if(props.text === "Red")
            cat.play();
        else
            lion.play();
    }

    return (
        <div className={`${cl}`}>
            <button onClick={change} style={props.bg}>
                {props.text}
            </button>
        </div>
    )
}

export default Button
